	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"My Brother Dave","date":"11 Aug 2006","slug":"/2006-08-11-my-brother-dave/","tags":"Y2006"},"html":"<p><embed src=\"http://odeo.com/flash/hellodeo_player.swf\" flashvars=\"external_url=http://media.odeo.com/9/8/2/711982.flv&thumb_url=http://images.odeo.com/5/1/7/1701623.jpeg&audio_id=1701623&audio_duration=5.669\" quality=\"high\" bgcolor=\"#ffffff\" width=\"230\" height=\"140\" name=\"hellodeo_player\" align=\"middle\" allowscriptaccess=\"always\" wmode=\"transparent\" type=\"application/x-shockwave-flash\" pluginspage=\"http://www.macromedia.com/go/getflashplayer\" /></p>\n<p>By the way, my brother's name isn't really Dave.</p>"},"next":{"fields":{"title":"It could be worse, it could be raining...","date":"11 Aug 2006","slug":"/2006-08-11-it-could-be-worse-it-could-be-raining/"}},"previous":{"fields":{"title":"Back Again","date":"18 Aug 2006","slug":"/2006-08-18-back-again/"}}},{"node":{"fields":{"title":"It could be worse, it could be raining...","date":"11 Aug 2006","slug":"/2006-08-11-it-could-be-worse-it-could-be-raining/","tags":"Y2006"},"html":"<p>For our return flight to Germany next week, not only do we have to worry about flight attendants creating <a href=\"http://www.nwaafa.org/default.asp?id=184\" title=\"CHAOS - Create Havoc Around Our System\">CHOAS</a> for Northwest Airlines but now we can no longer <a href=\"http://www.startribune.com/462/story/606764.html\" title=\"More passenger screening coming to MSP starting Friday\">carry liquids</a> in our carry-on luggage. It seems you don't actually have to commit a terrorist act to <a href=\"http://news.bbc.co.uk/2/hi/europe/4778931.stm\">disrupt airline travel</a>, merely talking about it within earshot of a police wiretap seems to be enough. But it could be <a href=\"http://www.educatedguesswork.org/movabletype/archives/2006/08/threat_modellin_1.html\">worse</a> (scroll to first comment)...</p>\n<blockquote><p>At least Richard Reed didn't strap C4 to his crotch, I'd hate to see what that would have done to the airport lines.</p></blockquote>\n<p>Ouch.</p>"},"next":{"fields":{"title":"Wherever we go, that's where we are","date":"04 Aug 2006","slug":"/2006-08-04-wherever-we-go-thats-where-we-are/"}},"previous":{"fields":{"title":"My Brother Dave","date":"11 Aug 2006","slug":"/2006-08-11-my-brother-dave/"}}},{"node":{"fields":{"title":"Wherever we go, that's where we are","date":"04 Aug 2006","slug":"/2006-08-04-wherever-we-go-thats-where-we-are/","tags":"Y2006"},"html":"<p><object width=\"425\" height=\"350\"><param name=\"movie\" value=\"http://www.youtube.com/v/foBRr-wjung\" /><embed src=\"http://www.youtube.com/v/foBRr-wjung\" type=\"application/x-shockwave-flash\" width=\"425\" height=\"350\" /></object></p>\n<p>Christopher fishing with Grandpa on his first day in Minnesota (<a href=\"http://www.youtube.com/watch?v=foBRr-wjung\">direct link</a>).</p>\n<p>I'll have to take back all the bad things I've said about Northwest Airlines. Our flight from Amsterdam to Minneapolis was our best flight ever to the States. The A330 is comfortable even in coach, and the entertainment system kept Christopher well occupied. And entry through immigration and customs in Minneapolis was also the fastest we've experienced, 10 minutes including waiting for luggage.</p>\n<p><strong>Update</strong></p>\n<p><a href=\"http://www.flickr.com/photos/papascott/206498186/\" title=\"Photo Sharing\"><img src=\"https://static.flickr.com/58/206498186_dc2cbb9fc4_m.jpg\" width=\"240\" height=\"209\" alt=\"IM000477\" /></a></p>\n<p>They caught one.</p>\n<p><strong>Update 2</strong></p>\n<p><iframe src=\"http://wikimapia.org/s/#y=45594822&x=-95484238&z=12&l=0&m=h\" width=\"324\" height=\"263\" frameborder=\"0\"></iframe></p>\n<p>This is where we are</p>"},"next":{"fields":{"title":"We're Outta Here","date":"02 Aug 2006","slug":"/2006-08-02-were-outta-here/"}},"previous":{"fields":{"title":"It could be worse, it could be raining...","date":"11 Aug 2006","slug":"/2006-08-11-it-could-be-worse-it-could-be-raining/"}}}],"pathPrefix":"","first":false,"last":false,"index":200,"pageCount":1101}}

